import Swiper, { Navigation } from "swiper";

import "swiper/css";
import "swiper/css/navigation";

Swiper.use([Navigation]);

function newSwiper() {
  const swiper = new Swiper(".swiper", {
    navigation: {
      nextEl: ".swiper-button-next",
      prevEl: ".swiper-button-prev",
    },
  });
}

export default newSwiper;
